@font-face {
    font-family: 'mfgenerator';
    src:url('fonts/mfgenerator.eot?nmokjp');
    src:url('fonts/mfgenerator.eot?#iefixnmokjp') format('embedded-opentype'),
    url('fonts/mfgenerator.woff?nmokjp') format('woff'),
    url('fonts/mfgenerator.ttf?nmokjp') format('truetype'),
    url('fonts/mfgenerator.svg?nmokjp#mfgenerator') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
    font-family: 'mfgenerator';
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

